<template>
  <header id="header">
    <mu-appbar title="亲子餐厅">
      <!-- <mu-icon-button icon="menu" slot="left"/> -->
      <mu-flat-button href="#app" label="首页" slot="right" />
      <mu-flat-button href="#about" label="关于我们" slot="right" />
      <mu-flat-button href="#service" label="服务" slot="right" />
      <mu-flat-button href="#environment" label="环境" slot="right" />
      <mu-flat-button href="#contact" label="联系我们" slot="right" />
      <mu-icon-menu icon="more_vert" slot="right" :anchorOrigin="leftBottom" :targetOrigin="leftTop">
        <mu-menu-item title="会员" />
        <mu-menu-item title="新闻" />
        <mu-menu-item title="招聘" />
      </mu-icon-menu>
    </mu-appbar>
  </header>
</template>
<script>
export default {
  data() {
    return {
      leftTop: {
        horizontal: 'left',
        vertical: 'top'
      },
      leftBottom: {
        horizontal: 'left',
        vertical: 'bottom'
      },
      rightBottom: {
        horizontal: 'right',
        vertical: 'bottom'
      },
      rightTop: {
        horizontal: 'right',
        vertical: 'top'
      }
    }
  }
}
</script>
<style lang="scss">
#header {
  position: fixed;
  width: 100%;
  z-index: 99;
}
</style>



