        <p>从友好的欢迎到愉快的再见，您可以相信，在这里您将有一整天的挑战性的活动，以启发和发展这些忙碌的头脑。</p>
      </div>
      <div class="details">
        <mu-flexbox :gutter="45" align="flex-start">
          <mu-flexbox-item>
            <div class="info">
              <h3>我们的故事</h3>
              <p>Rainbow Nursery first opened its doors in August 1989 and is registered with The Care Commission and approved by HMI. We are in partnership with West Lothian Council to offer grant assisted places for children aged 3-5 years. The nursery has been owned by a family from Glasgow since 2003 and serves the community of Bathgate and its surrounding areas. Rainbow nursery has expanded extensively over the past 10 years. The nursery now incorporates two buildings, which have been refurbished to the highest of standards.</p>
            </div>
          </mu-flexbox-item>
          <mu-flexbox-item>
            <div class="info">
              <h3>我们的发展</h3>
              <p>Fully refurbishing the 3 storey building which houses Toddlers, Tweenies, Babies and After School Club. Around the corner at the Old Library is our Early Learning & Childcare Centre. Viewing is a must at both buildings to realise full potential of quality care for your child. Our nurseries have large, bright, open plan rooms, which are designed as secure environments & allowing an abundance of space for children to enjoy varied sports, music, dance and yoga. Please contact us to arrange a viewing. </p>
            </div>
          </mu-flexbox-item>
          <mu-flexbox-item>
            <div class="info">
              <h3>我们的经理</h3>
              <p>Susan Johnston has been with Rainbow nursery for nearly 25 years, gaining all her qualifications here. She has been Nursery Manager since 2003 playing a key role in ongoing success of the nursery. Susan manages the current team effectively and is a great motivator, displaying a shared sense of direction, pride & energy and also recently completed her BA in Childcare. Leading our nursery teams who make the distinction in delivering outstanding customer service to our parents and unrivalled care to our children.</p>
            </div>
          </mu-flexbox-item>
